<template>
	<div :class="['board-square', { 'is-legal': isLegalMove, 'last-move': isLastMove }]" @click="handleMove">
		<div v-if="piece !== 0" :class="['piece', pieceClass, { 'flip-animate': shouldAnimate }]"
			@animationend="onAnimationEnd"></div>

		<div v-if="isLegalMove" class="hint-dot"></div>
	</div>
</template>

<script>
	export default {
		name: 'BoardSquare',
		props: {
			piece: {
				type: Number,
				required: true
			}, // 1=White, -1=Black, 0=Empty
			isLegalMove: {
				type: Boolean,
				default: false
			},
			isLastMove: {
				type: Boolean,
				default: false
			},
			coords: {
				type: Object,
				required: true
			} // { x, y } coordinates
		},
		data() {
			return {
				shouldAnimate: false, // „ÄêÊñ∞Â¢û„ÄëÊéßÂà∂Âä®ÁîªÁöÑÂºÄÂÖ≥
			};
		},
		computed: {
			pieceClass() {
				return this.piece === 1 ? 'white-piece' : (this.piece === -1 ? 'black-piece' : '');
			}
		},
		watch: {
			piece(newVal, oldVal) {
				// „ÄêÊñ∞Â¢û watch„Äë
				// Âè™ÊúâÂú®ÊóßÂÄºÂíåÊñ∞ÂÄºÈÉΩ‰∏ç‰∏∫ 0 ‰∏îÈ¢úËâ≤‰∏çÂêåÊó∂ÔºåÊâçËß¶ÂèëÂä®Áîª
				if (oldVal !== 0 && newVal !== 0 && oldVal !== newVal) {
					this.shouldAnimate = true;
				}
			}
		},
		methods: {
			handleMove() {
				if (this.isLegalMove) {
					// Âè™ÊúâÂêàÊ≥ïÁßªÂä®ÊâçËß¶Âèë‰∫ã‰ª∂
					this.$emit('make-move', this.coords);
				}
			},
			onAnimationEnd() {
				// „ÄêÊñ∞Â¢ûÊñπÊ≥ï„ÄëÂä®ÁîªÊí≠ÊîæÂÆåÊØïÂêéÔºåÁßªÈô§Á±ªÂêçÔºåÂáÜÂ§á‰∏ã‰∏ÄÊ¨°Âä®Áîª
				this.shouldAnimate = false;
			}
		}
	};
</script>

<style scoped>
	.board-square {
		/* ‰ΩøÁî® padding-bottom ÂÆûÁé∞Ëá™ÈÄÇÂ∫îÊ≠£ÊñπÂΩ¢ */
		width: 100%;
		padding-bottom: 100%;
		position: relative;
		background-color: #008000;
		/* ÁªøËâ≤ÊùøÂ≠ê */
		border: 1px solid #333;
		box-sizing: border-box;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.is-legal {
		cursor: pointer;
		background-color: #009900;
	}

	/* last-move Ê†áËÆ∞ */
	.last-move::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border: 3px solid rgba(255, 255, 0, 0.8);
		/* ÈªÑËâ≤Ê†áËÆ∞ */
		pointer-events: none;
		z-index: 5;
	}

	.piece {
		position: absolute;
		width: 80%;
		height: 80%;
		border-radius: 50%;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
		z-index: 10;
		top: 10%;
		/* transition: background-color 0.1s linear; */
		/* „ÄêÊñ∞Â¢û/‰øÆÊîπ„ÄëÊ∑ªÂä† background-color ËøáÊ∏°Ôºå‰ΩøÂÖ≥ÈîÆÂ∏ß‰πãÈó¥ÁöÑÈ¢úËâ≤ÂèòÂåñÊõ¥ÊüîÂíå */
		transition: background-color 0.8s ease-in-out;

		/* „ÄêÊñ∞Â¢û„ÄëÁ°Æ‰øù transform ÂßãÁªàÂ∫îÁî®‰∫é piece ÂÖÉÁ¥† */
		transform-origin: center;
	}

	.white-piece {
		background-color: white;
	}

	.black-piece {
		background-color: black;
	}

	.hint-dot {
		position: absolute;
		width: 20%;
		height: 20%;
		background-color: limegreen;
		border-radius: 50%;
		opacity: 0.8;
		z-index: 8;
		pointer-events: none;
		top: 40%
	}

	/* src/components/BoardSquare.vue (Âú® <style scoped> ‰∏≠) */

	/* ‰ºòÂåñÂêéÁöÑÂÖ≥ÈîÆÂ∏ßÂä®ÁîªÔºöÊ®°ÊãüÁøªËΩ¨„ÄÅÈ¢úËâ≤ÂÜ≤ÂáªÂíåÂºπË∑≥ */
	@keyframes flipImpact {

		/* 0%ÔºöÂàùÂßãÁä∂ÊÄÅ */
		0% {
			transform: scale(1.0) rotate(0deg) perspective(100px);
			opacity: 1;
			box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
		}

		/* 25%ÔºöÊî∂Áº©ÂíåÈ¢úËâ≤ÂÜ≤ÂáªÁÇπ (Áü≠ÊöÇÊòæÁ§∫ËøáÊ∏°Ëâ≤) */
		25% {
			/* Ê≤ø X ËΩ¥ÂéãÊâÅÔºåÊ®°ÊãüÁøªËΩ¨Âà∞‰æßÈù¢ */
			transform: scale(0.5, 1.2) rotate(30deg) perspective(100px);
			opacity: 0.7;
			/* È¢úËâ≤ÂÜ≤ÂáªÔºöÂø´ÈÄüËøáÊ∏°Âà∞‰∏Ä‰∏™‰∏≠ÊÄßËâ≤Ôºà‰æãÂ¶ÇÁÅ∞Ëâ≤Êàñ‰∫ÆËâ≤Ôºâ */
			background-color: #A9A9A9;
		}

		/* 50%ÔºöÊúÄÊâÅÂπ≥ÁÇπ/È¢úËâ≤ÂèòÂåñÂÆåÊàê (ËßÜËßâ‰∏äÊúÄÊ®°Á≥ä) */
		50% {
			transform: scale(0.1, 1.2) rotate(-30deg) perspective(100px);
			opacity: 0.9;
			/* Âú®ËøôÈáåËÉåÊôØÈ¢úËâ≤‰ºöË¢´Êñ∞ÁöÑ pieceClass Ë¶ÜÁõñÔºå‰ΩÜÂä®ÁîªÊïàÊûú‰ªçÂú®ÁªßÁª≠ */
			box-shadow: 0 0 10px rgba(255, 255, 0, 0.8);
			/* Èó™ÂÖâÊïàÊûú */
		}

		/* 75%ÔºöÂèçÂºπËÜ®ËÉÄÁÇπ (Êñ∞È¢úËâ≤ÔºåÂºπÁ∞ßÊïàÊûú) */
		75% {
			transform: scale(1.2, 1.1) rotate(10deg) perspective(100px);
			opacity: 1;
			box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
		}

		/* 100%ÔºöÊÅ¢Â§çÊúÄÁªàÁä∂ÊÄÅ */
		100% {
			transform: scale(1.0) rotate(0deg) perspective(100px);
			opacity: 1;
			box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
		}
	}

	/* Ëß¶ÂèëÂä®ÁîªÁöÑÁ±ª */
	.piece.flip-animate {
		/* „Äê‰øÆÊîπÊó∂Èïø„Äë0.8 ÁßíÔºå‰ª•ÈÖçÂêàÂâçÁ´ØÁöÑËäÇÂ•èÊéßÂà∂ */
		animation: flipImpact 0.8s ease-in-out;
	}
</style>
top: 10%